import Vue from 'vue';
import Router from 'vue-router';
import Fog from '@/components/pages/Fog'
import FogText from '@/components/pages/FogText'
import Detection from '@/components/pages/Detection'
import Information from '@/components/pages/Information'
import About from '@/components/pages/About'
import Home from '@/components/pages/Home'
import ContactForm from '@/components/pages/ContactForm'
import Books from '@/components/pages/information/books'
import Films from '@/components/pages/information/films'
import Movies from '@/components/pages/information/movies'
import Magazines from '@/components/pages/information/magazines'
import DetectionInfo from '@/components/pages/DetectionInfo'
import DetectionFamily from '@/components/pages/DetectionFamily'
import DetectionFriend from '@/components/pages/DetectionFriend'
import DetectionLove from '@/components/pages/DetectionLove'
import DetectionWork from '@/components/pages/DetectionWork'
import Section1 from '@/components/pages/fog/Section1'
import Section2 from '@/components/pages/fog/Section2'
import Section3 from '@/components/pages/fog/Section3'
import Section4 from '@/components/pages/fog/Section4'
import Section5 from '@/components/pages/fog/Section5'
import Section6 from '@/components/pages/fog/Section6'
import Section7 from '@/components/pages/fog/Section7'
import Section8 from '@/components/pages/fog/Section8'



Vue.use(Router)


let routes=[
  
  {
    path: '/',
    name: 'Home',
    component: Home,
  },  
  {
    path: '/Fog',
    name: 'Fog',
    component: Fog,
  },
  {
    path: '/Fog',
    name: 'Section1',
    component: Section1,
  },
  {
    path: '/Fog',
    name: 'Section2',
    component: Section2,
  },
  {
    path: '/Fog',
    name: 'Section3',
    component: Section3,
  },
  {
    path: '/Fog',
    name: 'Section4',
    component: Section4,
  },
  {
    path: '/Fog',
    name: 'Section5',
    component: Section5,
  },
  {
    path: '/Fog',
    name: 'Section6',
    component: Section6,
  },
  {
    path: '/Fog',
    name: 'Section7',
    component: Section7,
  },
  {
    path: '/Fog',
    name: 'Section8',
    component: Section8,
  },
  {
  path: '/FogText',
  name: 'FogText',
  component: FogText,
  },
  {
    path: '/Detection',
    name: 'Detection',
    component: Detection,
    redirect: '/Detection/Info',
    children: [
      {
        path: 'Info',
        name: 'Info',
        component: DetectionInfo
      },
      {
        path: 'Family',
        name: 'Family',
        component: DetectionFamily
      },
      {
        path: 'Friend',
        name: 'Friend',
        component: DetectionFriend
      },
      {
        path: 'Love',
        name: 'Love',
        component: DetectionLove
      },
      {
        path: 'Work',
        name: 'Work',
        component: DetectionWork
      },
    ]
  },
  {
    path: '/Information',
    name: 'Information',
    component: Information,
    redirect: '/Information/Books',
    children: [
      {
        path: 'Books',
        name: 'Books',
        component: Books
      },
      {
        path: 'Movies',
        name: 'Movies',
        component: Movies
      },
      {
        path: 'Films',
        name: 'Films',
        component: Films
      },
      {
        path: 'Magazines',
        name: 'Magazines',
        component: Magazines
      }
    ]
  },
  {
    path: '/About',
    name: 'About',
    component: About
  },
  {
    path: '/Contact',
    name: 'contact',
    component: ContactForm
  }

  
]

  export default new Router({
    mode: 'hash',
    routes
  })



